import  React from 'react';
import  styled from 'styled-components'
import  Footer from '../Footer.js';

import MaxWidthSection from '../../layout/MaxWidthSection';
import Row from '../../layout/Row';

const Work = styled(MaxWidthSection) `
margin-top: 15%;

`;

const ImgGrid = styled(Row)`
margin-top: 5%;
`;


const Complex = () => {
  return(
    <>
    <Work as="div" class="proj">
      <Row>
          <div class="intro">
                <div class="nombre">ComplexLand 2.0</div>
          </div>
          
          <div class="project-info">
                <div class="iz">
                  <p class="desc">(Role)</p><p class="subtag">Web / 3D / Motion</p>
                </div>
                <div class="der">
                  <p class="desc">(Agency)</p><p class="subtag">Jam3</p>
                  <p class="desc">(Date)</p><p class="subtag">2020</p>
                </div>
          </div>

          <div class="cover noselect">
            <img  src={require(`../../images/complexland.png`)} alt="" />
          </div>

          <div class="copy-project">
            <p>To recapture the cultural clout of ComplexCon in the age of isolation, 
              Jam3 and Complex Networks created Complexland: a first-of-its-kind gamified 
              web experience for ecommerce and culture.</p>
          </div>
      </Row>
      <ImgGrid as="div" class="case noselect" sd={1} ed={9} sm={1} em={6} ss={1} se={6}>
         <video width="100%" autoPlay muted loop >
          <source src={require('../../images/complex/complex-video.mp4')} type="video/mp4"/>
        </video>
      </ImgGrid>
      <ImgGrid as="div" class="case noselect" sd={4} ed={9} sm={1} em={6} ss={1} se={6}>
        <video width="100%" autoPlay muted loop >
          <source src={require('../../images/complex/complex-video2.mp4')} type="video/mp4"/>
        </video>
      </ImgGrid>
      
    </Work>
    <Footer></Footer>
    </>
  );

  

}

export default Complex;