import  React from 'react';
import  styled from 'styled-components'
import  Footer from '../Footer.js';

import MaxWidthSection from '../../layout/MaxWidthSection';
import Row from '../../layout/Row';

const Work = styled(MaxWidthSection) `
margin-top: 15%;

`;

const ImgGrid = styled(Row)`
margin-top: 5%;
`;


const Nola = () => {
  return(
    <>
    <Work as="div" class="proj">
      <Row>
          <div class="intro">
                <div class="nombre">NOLA 300</div>
          </div>
          
          <div class="project-info">
                <div class="iz">
                  <p class="desc">(Role)</p><p class="subtag">Product Design / Motion</p>
                </div>
                <div class="der">
                  <p class="desc">(Agency)</p><p class="subtag">tool of north america</p>
                  <p class="desc">(Date)</p><p class="subtag">2017</p>
                </div>
          </div>

          <div class="cover noselect">
            <img  src={require(`../../images/nola.png`)} alt="" />
          </div>

          <div class="copy-project">
            <p>The New Orleans Story Booth was an interactive art experience that used artificial intelligence (AI) 
                and film projections to collect and share the city’s best stories.</p>
          </div>
      </Row>
      <ImgGrid as="div" class="case noselect" sd={1} ed={11} sm={1} em={6} ss={1} se={6}>
        <img  src={require(`../../images/nola/logo2.png`)} alt="" />
      </ImgGrid>
      <ImgGrid as="div" class="case noselect" sd={9} ed={4} sm={1} em={6} ss={1} se={6}>
        <img  src={require(`../../images/nola/logo1.png`)} alt="" />
      </ImgGrid>
      <ImgGrid as="div" class="case noselect" sd={1} ed={4} sm={1} em={6} ss={1} se={6}>
        <img  src={require(`../../images/nola/boothexterior.png`)} alt="" />
      </ImgGrid>
      <ImgGrid as="div" class="case noselect" sd={7} ed={5} sm={1} em={6} ss={1} se={6}>
          <div class="copy-description">
            <p>I collaborated with the team creating the UI and motion of the app that 
              people had to use when they entered the installation. We came up with a 
              system that keep the escense otheir branding that wasn’t plan for digital 
              formats while using our new shoots and style.</p>
          </div>
      </ImgGrid>
      <ImgGrid as="div" class="case noselect" sd={1} ed={5} sm={1} em={6} ss={1} se={6}>
        <img  src={require(`../../images/nola/font.png`)} alt="" />
      </ImgGrid>
      <ImgGrid as="div" class="case noselect" sd={4} ed={9} sm={1} em={6} ss={1} se={6}>
        <img  src={require(`../../images/nola/colors.gif`)} alt="" />
      </ImgGrid>
      <Row>
       <video  width="100%" autoPlay muted loop  >
          <source src={require('../../images/nola/nola-video.mp4')} type="video/mp4"/>
        </video>
      </Row>
      <ImgGrid as="div" class="case noselect" sd={1} ed={9} sm={1} em={6} ss={1} se={6}>
        <img  src={require(`../../images/nola/app.png`)} alt="" />
      </ImgGrid>
      <ImgGrid as="div" class="case noselect" sd={10} ed={3} sm={1} em={6} ss={1} se={6}>
        <img  src={require(`../../images/nola/booth-app.png`)} alt="" />
      </ImgGrid>

      <ImgGrid as="div" class="case noselect" sd={1} ed={12} sm={1} em={6} ss={1} se={6}>
        <img  src={require(`../../images/nola/nola-exterior.png`)} alt="" />
      </ImgGrid>

      
    </Work>
    <Footer></Footer>
    </>
  );

  

}

export default Nola;