import  React from 'react';
import styled from 'styled-components'


import FullWidthSection from '../layout/FullWidthSection';
import MaxWidthSection from '../layout/MaxWidthSection';
import Row from '../layout/Row';

const Content = styled(MaxWidthSection) `
padding: 1.25rem 1.25rem; 
`;

const Hero = () => {
    return(
        <Content as="main">
            <Row>
                <div className='text'>
                    <p class="copy">Juan Castro</p><br></br>
                    <p class="copy">Interactive Designer</p><br></br>
                    <p class="title">selected works/22</p>
                </div>
            </Row>
        </Content>
    );

}

export default Hero;