import  React from 'react';
import styled from 'styled-components'

import { Link } from "react-router-dom";

import MaxWidthSection from '../layout/MaxWidthSection';
import Row from '../layout/Row';

import Hero from './Hero.js';
import Footer from './Footer.js';
import Info from './Info.js';

//const transition = { duration: 0.6, ease: [0.43, 0.13, 0.23, 0.96] };

const Content = styled(MaxWidthSection) `
 
`;

const Project = styled(Row)`
margin-top: 5%;
`;

const Home = () => (


        <>
        <Hero />
        
        <Content as="section">
            
           
            <Project as="div" class="case" sd={5} ed={9} sm={1} em={6} ss={1} se={6}>
                <Link to={`/ComplexLand`}>
                    <img src={require(`../images/complexcover.png`)} alt="" />      
                </Link>
                <p class="tag"> Web design, 3d, Motion</p>
            </Project>
           
            <Project as="div" class="case" sd={1} ed={5} sm={1} em={6} ss={1} se={6}>
                <Link to={`/Nola300`}>
                    <img src={require(`../images/nolacover.png`)} alt="" />
                </Link>
                <p class="tag"> PRODUCT DESIGN, MOTION</p>
            </Project>

            <Project as="div" class="case" sd={8} ed={4} sm={1} em={6} ss={1} se={6}>
                <img src={require(`../images/cover.png`)} alt="" />
                <p class="tag"> 3d, motion</p>
            </Project>
            <Project as="div" class="case" sd={1} ed={6} sm={1} em={6} ss={1} se={6}>
                <img src={require(`../images/complexcover.png`)} alt="" />
                <p class="tag"> Web, 3d, Motion</p>
            </Project>

        </Content>

        <Footer/>
        </>

    );

    

export default Home;